.App {
  display: flex;
  justify-content: center;
  flex-direction: column;
  max-width: 1000px;
  margin: 0 auto;
  padding-top: 100px;
  padding-bottom: 100px;
}

h1 {
  font-family: "Roboto Slab";
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  color: rgba(0,0,0,.8);
}
h2 {
  font-family: "Roboto Slab";
  font-weight: bold;
  color: rgba(0,0,0,.8);
  margin: 40px 0;
}

p {
  font-family: "Roboto Slab";
  font-weight: normal;
}

.flex {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}
.flex p {
  margin: 0;
}

.checkbox {
  height: 40px;
  width: 40px;
  min-width: 40px;
  margin-right: 15px;
  position: relative;
  cursor: pointer;
  border: 2px solid #93c8d3;
}
.checkbox:hover {
  border: 2px solid orange;
}
.checkbox.active {
  border: 2px solid orange;
}
.checkbox.active::after {
  content: '';
  position: absolute;
  width: 36px;
  height: 36px;
  top: 2px;
  left: 2px;
  background: orange;
}
input {
  width: 100%;
  border: 2px solid #93c8d3;
  height: 40px;
  padding: 0 25px;
  margin-top: 10px;
  font-family: "Roboto Slab";
  font-weight: normal;
}
.inputBox p {
  margin-bottom: 0;
}
a {
  opacity: 0;
  height: 0;
  width: 0;
}
.inputBox span {
  color: rgba(0,0,0,.5)
}
.inputBox.space-between {
  justify-content: space-between;
}
.inputBox.space-between > div {
  display: flex;
  align-items: center;
}
input:focus {
  outline: #93c8d3;
  border: 2px solid orange;
}
.save {
  padding: 10px 35px;
  background: orange;
  width: 150px;
  text-align: center;
  margin: 30px auto 0;
  color: white;
  cursor: pointer;
}

.modalBox {
  width: 100vw;
  height: 0;
  position: fixed;
  background: rgba(0,0,0,.7);
  transition: 0.3s ease;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modalBox p {
  padding: 25px 45px;
  background: white;
  border-radius: 3px;
  opacity: 0;
  transition: 0.3s ease-in 0.3s;
}
.modalBox.open p {
  position: relative;
  opacity: 1;
}
.modalBox.open span {
  width: 20px;
  height: 20px;
  top: 0;
  right: 0;
  position: absolute;
  border-radius: 50%;
  background: white;
  cursor: pointer;
}
.modalCircle {
  position: fixed;
  background: white;
  transition: 0.3s ease;
  width: 0px;
  height: 0px;
  transform-origin: center;
  border-radius: 50%;
  bottom: 10px;
  left: 30px;
}

@keyframes circle {
  0% {
    width: 40px;
    height: 40px;
  }
  50% {
    width: 45px;
    height: 45px;
  }
  100% {
    width: 40px;
    height: 40px;
  }
}

.modalCircle.open {
  animation: circle 1.5s ease infinite;
}
.modalBox.open {
  height: 100vh;
}

@media screen and (max-width: 1000px) {
  .App {
    padding: 50px 15px;
  }
  input {
    padding: 0 15px;
    width: calc(100% - 35px)
  }
  .checkbox {
    height: 20px;
    width: 20px;
    min-width: 20px;
    margin-right: 15px;
    position: relative;
    cursor: pointer;
    border: 2px solid #93c8d3;
  }
  .checkbox.active::after {
    content: '';
    position: absolute;
    width: 16px;
    height: 16px;
    top: 2px;
    left: 2px;
    background: orange;
  }
  img {
    max-width: 100px;
    float: right;
  }
  .modalCircle {
    display: none;
  }
  .modalBox .modal {
    max-width: 320px;
  }
  .modalBox p {
    text-align: center;
  }
}
